.title {
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: var(--title-color);
}

.text {
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
}

.info{
    display: flex;
    flex-direction: column;
}

.wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 25px;
    margin-bottom: 20px;
}

.list {
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: wrap;
}

#currency {
    background-color: var(--currencies-color);
    margin-top: 5px;
}

#languages {
    background-color: var(--languages-color);
    margin-top: 5px;
}

#phone {
    background-color: var(--phone-color);
    margin-top: 5px;
}

#currency, #languages, #phone {
    text-align: center;
    padding: 2px;
    border-radius: 15px;
    padding-inline: 10px;
}

@media (min-width: 780px) {
    #phone,#languages, #currency  {
        background: none;
        text-align: center;
    }
    .list{
        justify-content: center;
        gap: 0;
    }
    .info{
        align-items: center;
    }
    .title{
        text-align: center;
    }
    .wrapper{
        gap: 5px;
        align-items: center;
        margin-bottom: 0;
    }
    .text{
        font-size: 30px;
        line-height: 40px;
        text-align: center;
    }
    .country, .languages, .region {
        align-self: end;
    }
    .capital, .currency, .phone {
        align-self: start;
    }
    .country > .wrapper,
    .capital > .wrapper,
    .region > .wrapper{
        flex-direction: column-reverse;
    }
    .currency > .wrapper,
    .languages > .wrapper,
    .phone > .wrapper{
        flex-direction: column;
    }
}

@media (min-width: 781px) and (max-width: 1025px) {
    .text{
        font-size: 25px;
        line-height: 35px;
        text-align: center;
    }
}



